.App {
  padding: 10px;
}

.textToType,
.typedText {
  font-family: "Courier New", Courier, monospace;
  font-size: 20px;
  border: 0px;
}

.textToType {
  margin-left: 3px;
  margin-bottom: 5px;
}

.correct {
  background-color: limegreen;
}

.wrong {
  background-color: orangered;
}

td {
  padding-right: 10px;
}

td:first-child {
  text-align: right;
}
